<template>
  <div v-zinx="{ page: 'limit' }" class="page collapse">
    <div class="wrapper-mask"></div>
    <div class="collapse_show">
      <div class="collapse_kefu">
        <div v-zoom class="collapse_kefu_btn c_box cursor_point" @click="onJump">
          <icon-font name="icon-Icon_Service2" :size="commonStyle('collapse_kefu_icon_size')" />
        </div>
      </div>
      <div class="collapse_content column_flex">
        <img class="collapse_content_logo" :src="$loadIcon('logo_lg')" alt="" />
        <div class="collapse_text">
          {{ $t('UI_collapse_text') }}
        </div>
      </div>
      <div class="collapse_footer column_flex">
        <v-btn
          class="btn btn_radius"
          color="t_primary_dark_color"
          :height="commonStyle(isApp ? 'collapse_footer_btn_app_height' : 'collapse_footer_btn_pc_height')"
          block
          @click="onRefresh"
        >
          {{ $t('UI_limit_btn') }}
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { VUE_APP_KF_LINK } from '@/api/setting.js'
import _common from '@style/views/limitation.modules.scss'

export default {
  name: 'collapse',
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 跳转客服
    onJump() {
      window.location.href = VUE_APP_KF_LINK || ''
    },
    // 刷新
    onRefresh() {
      window.location.reload()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/limitation.modules.scss';

// 针对移动端样式
.wrapper-mask {
  background: $collapse_wrapper_bg_pc !important;
}
.collapse_show {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $collapse_bg_app;
  .collapse_kefu {
    @include setBox(row, flex-end, center);
    padding: $collapse_kefu_app_padding;
    .collapse_kefu_btn {
      width: $collapse_kefu_width;
      height: $collapse_kefu_height;
      background: $collapse_kefu_bg;
      border-radius: $collapse_kefu_radius;
    }
  }
  .collapse_content {
    margin: $collapse_content_margin;
    .collapse_content_logo {
      width: $collapse_content_app_logo_width;
    }
    .collapse_text {
      width: $collapse_content_app_text_width;
      margin: $collapse_content_app_text_margin;
      font-size: $collapse_content_app_text_font_size;
      font-weight: $collapse_content_app_text_font_weight;
    }
  }
  .collapse_footer {
    margin: $collapse_footer_app_margin;
    width: $collapse_footer_app_width;
    .btn {
      font-size: $collapse_footer_btn_app_font_size !important;
      font-weight: $collapse_footer_btn_app_font_weight !important;
    }
  }
}

// 针对pc样式
@include breakpoint-only-up($px: $collapse_pc_width) {
  .collapse_show {
    width: $collapse_pc_width;
    height: $collapse_pc_height;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    transform: translate(-50%, -50%);
    background: $collapse_bg_pc;
    border-radius: $collapse_pc_radius;
    .collapse_kefu {
      padding: $collapse_kefu_pc_padding;
    }
    .collapse_content {
      margin: auto;
      .collapse_content_logo {
        width: $collapse_content_pc_logo_width;
      }
      .collapse_text {
        width: $collapse_content_pc_text_width;
        margin: $collapse_content_pc_text_margin;
        font-size: $collapse_content_pc_text_font_size;
        font-weight: $collapse_content_pc_text_font_weight;
      }
    }
    .collapse_footer {
      margin: $collapse_footer_pc_margin;
      width: $collapse_footer_pc_width;
      .btn {
        font-size: $collapse_footer_btn_pc_font_size !important;
        font-weight: $collapse_footer_btn_pc_font_weight !important;
      }
    }
  }
}
</style>
